<template>

  <div class="container" v-loading="isLoading">
    <div class="App">
      <h1 style="font-size: 18px; color: #999999">{{ title }}</h1>
      <span style="font-size: 20px; ">{{ desc }}</span>

      <div v-show="!getQueryTl">

        <div v-show="localLanguage.name != null">
          <h1 style="font-size: 18px; color: #999999">Local Languages</h1>
          <div @click="handleLanguage(localLanguage)" class="Option">
            <a>{{ localLanguage.name }}</a>
          </div>

          <h1 style="font-size: 18px; color: #999999">More languages</h1>
        </div>

        <div @click="handleLanguage(item)" class="Option" v-for="item in languages">
          <a>{{ item.name }}</a>
        </div>
      </div>


      <div v-show="getQueryTl && !showResult">

        <span style="font-size: 18px; color: #999999">You do not need to enter all translations before submitting, partial submissions are accepted.</span>

        <div class="Input" v-for="item in strings">
          <h3>{{ item.en }}</h3>
          <textarea v-if="60 < item.en.length" dir="auto" v-model="item.value" placeholder=""/>
          <input v-if="item.en.length <= 60" type="text" v-model="item.value" placeholder=""/>
        </div>
      </div>

      <el-button v-show="getQueryTl && !showResult" :loading="isSubmitLoading" class="Button" @click="handleSubmit">Submit</el-button>

      <div style="height: 200px; margin-top: 60px;">
        <h4 style="text-align: center; color: #999">@Power by AppLocalize.top</h4>
      </div>

    </div>
  </div>

</template>

<script>
import {getLanguages, getStrings, submitStrings} from "../api/user"
export default {
  data() {
    return {
      title: 'Help Translate',
      desc: 'Choose a language',
      isLoading: true,
      targetField: 'en',
      showResult: false,
      isSubmitLoading: false,
      localLanguage: {},
      languages: [],
      strings: []
    }
  },
  mounted() {
      this.requestServerData()
  },
  watch: {
    '$route.query.tl': {
      handler(newVal, oldVal) {
        this.requestServerData()
      }
    }
  },
  methods: {
    handleLanguage(item) {
      this.$router.push({
        path: "/translate/" + this.$route.params.id,
        query: {tl: item.key}
      })
    },
    handleSubmit() {
      this.isSubmitLoading = true
      const request = {
        "projectKey": this.$route.params.id,
        "srcLanguage": "en",
        "toLanguage": this.$route.query.tl,
        "data": []
      }

      this.strings.forEach((item) => {
        if (item.value !== undefined && item.value !== "" && item.value !== item[this.targetField]) {
          const d = {"transId": item.transId, "value": item.value}
          request.data.push(d)
        }
      })
      submitStrings(request).then((data) => {
        this.isSubmitLoading = false
        if (data.code == 200) {
          this.desc = "Submitted successfully, thank you for your contribution.";
          this.showResult = true;
        }
      })

    },
    requestServerData() {
      const projectKey = this.$route.params.id;
      const query = this.getQueryTl;
      const request = {"key": projectKey, "targetKey": query}
      this.showResult = false;
      this.isLoading = true

      if (projectKey !== undefined && query !== undefined) {
        this.title = ""
        this.desc = ""
        getStrings(request).then((data) => {
          this.isLoading = false
          if (data.code == 200) {
            this.targetField = data.data.targetField;
            data.data.strings.forEach((item) => {
              item.value = item[this.targetField]
            })
            this.strings = data.data.strings;
            this.title = data.data.projectName;
            this.desc = "Submit translations for " + data.data.targetLanguage
          }
        })
      } else {
        this.title = "Help Translate"
        this.desc = "Choose a language"
        getLanguages(request).then((data) => {
          this.isLoading = false
          if (data.code == 200) {
            this.languages = data.data.sort((a, b) => {
              return a.name.localeCompare(b.name)
            });
            const localLanguageKey = navigator.language.split("-")[0].toLowerCase()
            this.languages.forEach((item) => {
              let lk = item.key.split("_")[0].toLowerCase()
              if (localLanguageKey.includes(lk)) {
                console.log(localLanguageKey,  lk);
                this.localLanguage = item
              }
            })
          }
        })
      }
    }
  },

  computed: {
    getQueryTl() {
        return this.$route.query.tl
    }
  }
}
</script>

